import { Badge, Calendar } from "antd";
import React, { useContext, useEffect, useState } from "react";
import Header from "../../header/Header";
import dayjs from "dayjs";
import { CrudContext } from "../../../context/CrudContext";
import ModalRealizarPago from "./ModalRealizarPago";

const GenerarPago = () => {
	const { getData, setModal, modal } = useContext(CrudContext);

	const [mes, setMes] = useState(dayjs().month());
	const [diasMes, setDiasMes] = useState(dayjs().daysInMonth());

	const [pagosMes, setPagosMes] = useState([]);

	const onChange = (e) => {
		setMes(e.month());
		setDiasMes(e.daysInMonth());
	};

	const getPagosMes = async () => {
		const pagos = [];
		for (let index = 1; index <= diasMes; index++) {
			const dia = index < 10 ? `0${index}` : index;
			const mesFormat = mes < 10 ? `0${mes + 1}` : mes + 1;
			const existePagoDia = await getData(
				`pago?fecha=${dayjs().year()}-${mesFormat}-${dia}`
			);
			if (existePagoDia.data.length > 0) {
				pagos.push(index);
			}
		}
		setPagosMes(pagos);
	};

	useEffect(() => {
		getPagosMes();
	}, [diasMes]);

	const [fechaPago, setFechaPago] = useState("");
	const [data, setData] = useState([]);

	const getFechaPago = async (fecha) => {
		const route = `pago?fecha=${fecha}`;
		const response = await getData(route);

		if (response.data.length > 0) {
			setData(response.data);
			setModal(true);
		}
	};

	const onSelect = async (e) => {
		const fecha = dayjs(e).format("YYYY-MM-DD");
		setFechaPago(fecha);

		getFechaPago(fecha);
	};

	const dataCellRender = (value) => {
		const listData = pagosMes.includes(value.date());
		return <>{listData && <Badge status={"success"} text="Pagos" />}</>;
	};

	return (
		<>
			<Header text={"Realizar pagos"} ruta={"/planilla"} />
			<div className="margenes">
				<Calendar
					fullscreen={true}
					onSelect={onSelect}
					onChange={onChange}
					dateCellRender={dataCellRender}
				/>
			</div>
			{modal && (
				<ModalRealizarPago
					fecha={fechaPago}
					data={data}
					actualizarTabla={getPagosMes}
				/>
			)}
		</>
	);
};

export default GenerarPago;