import { Table } from "antd";
import React from "react";
import DataTable from "react-data-table-component";

import "./tabla.css";

const TablaPagosDiarios = ({ columns, table, filas }) => {
	const columnsExpandable = columns.map((item) => {
		return {
			title: item.name,
			dataIndex: item.id,
			key: item.id,
		};
	});

	const data = table.map((item) => {
		let disabled = false;
		if (
			item?.pagos?.trabajadores?.length < 2 &&
			item.tipo !== "asociacion"
		) {
			disabled = true;
		}

		return {
			...item,
			disabled,
		};
	});

	const paginationComponentOptions = {
		rowsPerPageText: "Filas por página",
		rangeSeparatorText: "de",
		selectAllRowsItem: false,
		selectAllRowsItemText: "Todos",
	};

	const ExpandedComponent = ({ data }) => (
		<Table
			size="small"
			columns={columnsExpandable.filter(
				(item) =>
					item.key === "Nro" ||
					item.key === "nombre" ||
					item.key === "pago" ||
					item.key === "dni" ||
					item.key === "telefono"
			)}
			pagination={false}
			dataSource={
				data.tipo === "asociacion"
					? data.pagos?.trabajadores?.map((item, index) => {
							return {
								Nro: index + 1,
								nombre: item.nombre,
								pago:
									parseFloat(item.teletrans) === 0
										? "-"
										: item.teletrans,
								dni: item.dni,
								telefono: item.telefono,
							};
					  })
					: data.tipo === "pago"
					? data.pagos?.trabajadores?.map((item, index) => {
							return {
								Nro: index + 1,
								nombre: item.nombre,
								pago: item.teletrans,
								dni: item.dni,
								telefono: item.telefono,
							};
					  })
					: data.tipo === "incentivo"
					? data.pagos?.trabajadores?.map((item, index) => {
							return {
								Nro: index + 1,
								nombre: item.nombre,
								pago: item.teletrans,
								dni: item.dni,
								telefono: item.telefono,
							};
					  })
					: data.pagos?.trabajadores?.map((item, index) => {
							return {
								Nro: index + 1,
								nombre: item.nombre,
								pago: item.teletrans,
								dni: item.dni,
								telefono: item.telefono,
							};
					  })
			}
		/>
	);

	return (
		<div className="table-container">
			<DataTable
				size="small"
				columns={columns.filter(
					(item) => item.id !== "dni" && item.id !== "telefono"
				)}
				data={data}
				pagination
				striped
				highlightOnHover
				responsive
				paginationComponentOptions={paginationComponentOptions}
				paginationPerPage={filas || 8}
				paginationRowsPerPageOptions={[8, 16, 24, 32, 40]}
				expandableRows
				expandableRowsComponent={ExpandedComponent}
				expandableRowDisabled={(row) => row.disabled}
			/>
		</div>
	);
};

export default TablaPagosDiarios;
